.App {
  text-align: center;
  height: 100vh;
  /* background-color: #3c454f; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Boxes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 5vh 10vh 0 10vh;
}

.Alert {
  margin: 3vh 10vh 0 10vh;
}

.contactAttributes {
  margin: 0vh 10vh;
}

.Box {
  width: 15%;
  margin: 15px;
  padding: 20px;
  height: 18vh;
  border-radius: 5px;
  background-color: lightgray;
  color: black;
  z-index: 0;
}

.BoxTitle {
  padding: 2vh 0px 0px 0px;
  font-size: 10px;
}

#BoxImage {
  z-index: 1;
  aspect-ratio: 1 / 1;
  width: 10%;
}